.rw-listbox {
    background: #fff;
    overflow: hidden;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border: 1px solid #e5e5ea;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
}
.rw-listbox:focus {
    outline: 0;
}
.rw-listbox-disabled {
    border: 1px solid #b3b3b3;
}
.rw-listbox .rw-listbox-body {
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    overflow: hidden;
    overflow-y: auto;
}
.rw-listbox .rw-listbox-body .rw-listbox-item-group-title,
.rw-listbox .rw-listbox-body .rw-listbox-item {
    position: relative;
    padding: 8px 12px;
    cursor: pointer;
    line-height: auto;
    height: auto;
    white-space: nowrap;
    overflow: hidden;
}
.rw-listbox .rw-listbox-body .rw-listbox-item-active {
    background: rgba(235, 249, 255, 0.5);
}
.rw-listbox .rw-listbox-body .rw-listbox-item-selected {
    background: #ebf9ff;
}
.rw-listbox .rw-listbox-body .rw-listbox-item-disabled {
    opacity: 0.5;
    cursor: default;
}
.rw-listbox .rw-listbox-body .rw-listbox-item-disabled:not(.rw-listbox-item-selected):hover {
    background: none;
}
.rw-listbox .rw-listbox-body .rw-listbox-item-group-title {
    padding: 8px 12px;
    cursor: default;
    font-weight: 700;
}
.rw-listbox .rw-listbox-body .rw-listbox-item-group-list .rw-listbox-item {
    padding-left: 28px;
}
